}

body {
  font-family: 'Open Sans', sans-serif; }

body, html, div#root {
  height: 100%; }

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  height: 100%; }

.map-container {
  -webkit-flex: 2 1;
          flex: 2 1;
  height: 100%;
  position: relative; }

.info-pane {
  -webkit-flex: 1 1;
          flex: 1 1;
  background-color: #f5f4f8;
  overflow-y: scroll; }

.flight-item.highlight {
  border: 3px solid #41ba95;
  border-radius: 4px; }

.flight-item {
  margin: 10px;
  background: #ffffff;
  padding: 10px;
  cursor: pointer; }
  .flight-item .flight-details {
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center; }
    .flight-item .flight-details .airline-icon {
      text-align: center;
      padding-right: 10px; }
    .flight-item .flight-details .airline-icon img {
      border-radius: 3px; }
    .flight-item .flight-details .proximity {
      text-align: right;
      font-size: 1.1rem;
      font-weight: bold;
      -webkit-flex: 1 1;
              flex: 1 1; }
    .flight-item .flight-details .title {
      display: block;
      font-weight: bold; }
    .flight-item .flight-details .subtitle {
      font-size: 0.9rem;
      color: #555; }
  .flight-item .metrics {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    font-size: 0.9rem;
    margin-top: 10px; }
  .flight-item .metrics > div {
    -webkit-flex: 1 1;
            flex: 1 1; }
  .flight-item .metrics .title {
    font-weight: bold;
    display: block;
    line-height: 1.8; }
  .flight-item .metrics .subtitle {
    color: #555; }
  .flight-item .route {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    border-bottom: 1px solid #eee;
    padding: 10px 0; }
    .flight-item .route .location {
      -webkit-flex: 1 1;
              flex: 1 1; }
    .flight-item .route .destination {
      text-align: right; }
    .flight-item .route .title {
      display: block;
      font-weight: bold;
      font-size: 0.9rem; }
    .flight-item .route .subtitle {
      font-size: 0.9rem;
      color: #555; }
    .flight-item .route .route-progress {
      -webkit-flex: 3 1;
              flex: 3 1;
      padding-left: 20px;
      padding-right: 20px;
      -webkit-align-self: center;
              align-self: center; }
    .flight-item .route .progress {
      display: -webkit-flex;
      display: flex;
      height: 10px;
      width: 100%;
      overflow: hidden;
      font-size: .75rem;
      background-color: #e9ecef;
      border-radius: .25rem; }
    .flight-item .route .progress-bar {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-justify-content: center;
              justify-content: center;
      color: #fff;
      text-align: center;
      white-space: nowrap;
      background-color: #28a745;
      transition: width .6s ease; }

.alert {
  background-color: #41ba95;
  color: #FFFFFF;
  padding: 10px;
  margin-bottom: 10px; }

.weather-pane {
  position: absolute;
  bottom: 24px;
  right: 70px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px -1px;
  border-radius: 2px;
