@import url('https://fonts.googleapis.com/css?family=Open+Sans');
body{
  font-family: 'Open Sans', sans-serif;
}
body, html, div#root{
  height: 100%;
}
.container{
  display: flex;
  flex-direction: row;
  height: 100%;
}
.map-container{
  flex: 2;
  height: 100%;
  position: relative;
}
.info-pane{
  flex: 1;
  background-color: #f5f4f8;
  overflow-y: scroll;
}
.flight-item.highlight{
  border: 3px solid #41ba95;
  border-radius: 4px;
}
.flight-item{
  margin: 10px;
  background: #ffffff;
  padding: 10px;
  cursor: pointer;

  .flight-details{
    border-bottom: 1px solid #eee;
    padding-bottom: 10px;
    display: flex;
    align-items: center;
    .airline-icon{
      text-align: center;
      padding-right: 10px;
    }
    .airline-icon img{
      border-radius: 3px;
    }
    .proximity{
      text-align: right;
      font-size: 1.1rem;
      font-weight: bold;
      flex: 1;
    }
    .title{
      display: block;
      font-weight: bold;
    }
    .subtitle{
      font-size: 0.9rem;
      color: #555;
    }
  }

  .metrics{
    display: flex;
    flex-direction: row;
    font-size: 0.9rem;
    margin-top: 10px;
  }
  .metrics > div{
    flex: 1;
  }
  .metrics .title{
    font-weight: bold;
    display: block;
    line-height: 1.8;
  }
  .metrics .subtitle{
    color: #555;
  }

  .route{
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #eee;
    padding: 10px 0;

    .location{
      flex: 1;
    }
    .destination{
        text-align: right;
    }
    .title{
      display: block;
      font-weight: bold;
      font-size: 0.9rem;
    }
    .subtitle{
      font-size: 0.9rem;
      color: #555;
    }
    .route-progress{
      flex: 3;
      padding-left: 20px;
      padding-right: 20px;
      align-self: center;
    }
    .progress{
      display: flex;
      height: 10px;
      width: 100%;
      overflow: hidden;
      font-size: .75rem;
      background-color: #e9ecef;
      border-radius: .25rem;
    }
    .progress-bar{
      display: flex;
      flex-direction: column;
      justify-content: center;
      color: #fff;
      text-align: center;
      white-space: nowrap;
      background-color: #28a745;
      transition: width .6s ease;
    }
  }
}


//
.alert{
    background-color: #41ba95;
    color: #FFFFFF;
    padding: 10px;
    margin-bottom: 10px;
}
// Weather
.weather-pane{
  position: absolute;
  bottom: 24px;
  right: 70px;
  background: #fff;
  box-shadow: rgba(0, 0, 0, 0.3) 0px 1px 4px -1px;
  border-radius: 2px;
  padding: 10px;
}
